*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
html,body{
  height:100%;
}
body {
  margin: 0;
  text-align: center;
  height:100%;
}
#root{
  height: 100%;
}

h1{
  padding-top: 20px;
  color:white;
}

button{
  margin:20px;
  padding:10px;
  font-size: 20px;
  border-radius: 0px 20% 0px 20%;
  outline:none;
  font-weight: bold;
  color:rgb(247, 72, 101);
  border: 2px solid rgb(247, 72, 101);
}

